body, h1 {
  font-family: Copperplate, sans-serif;
}

h3 {
  font-size: 35px;
  font-weight: 700;
}

h1 {
  text-align: center;
  font-size: 100px;
}

h2 {
  border-width: 50px solid;
  color: #000;
  padding: 10px;
  font-size: 55px;
}

header {
  color: #fff;
  background-image: linear-gradient(to top, #dd5e5e, #bc5454, #f23b3b);
  padding: 30px;
}

.navbar {
  min-height: 80px;
  padding: 20px;
  font-size: 20px;
}

.navbar-nav {
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  list-style: none;
  display: flex;
}

.navbar-nav li {
  margin: 0 35px;
}

.navbar-nav ul {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

header h2 {
  color: #fff;
}

footer {
  color: #fff;
  background-image: linear-gradient(#c84040, #bc5454, #f23b3b);
  padding: 20px 0;
}

.row-content {
  border-bottom: 1px ridge;
  min-height: 200px;
  padding: 50px 0;
}

.navbar {
  background-color: #aba058;
}

.tab-content {
  border: 1px solid #0f0f0f;
  border-top: none;
  padding: 10px;
}

.accordion-button {
  background-color: #070606;
}

.carousel-caption {
  color: #fffdfd;
  background-color: #050404b3;
  border-radius: 10px;
  width: 80%;
  max-width: 90%;
  padding: 10px;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
}

.carousel {
  border: 5px solid #000;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 1500px;
  max-width: 100%;
  height: auto;
  margin: auto;
  position: relative;
}

.carousel img {
  width: 100%;
  display: block;
}

.icons {
  color: #fff;
}

.carousel-control-next-icon, .carousel-control-prev-icon {
  background-color: #1d1d1d;
  border-radius: 50%;
  width: 50px;
  height: 50px;
}

#homeCarousel {
  align-items: center;
}

p {
  padding: 10px;
  font-size: 25px;
}

li {
  font-size: 20px;
}

form {
  background: #f45c5c;
  border-radius: 10px;
  max-width: 1000px;
  margin: auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

input[type="text"], input[type="email"], textarea {
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin: 6px 0;
  padding: 10px;
}

textarea {
  height: 250px;
}

input[type="submit"] {
  color: #fff;
  cursor: pointer;
  float: right;
  background-color: #c42121;
  border: none;
  border-radius: 5px;
  padding: 20px 50px;
  font-size: 25px;
}

input[type="submit"]:hover {
  background-color: #a12a2a;
}

.nucamplogo {
  max-width: 150px;
  max-height: 150px;
}

.uhlogo {
  max-width: 100px;
  max-height: 100px;
}

body {
  background-image: linear-gradient(#e86969, #bc5454, #f23b3b);
}

label {
  font-size: 25px;
}
